<template>  
  <div class="customInput">
      <header>
        <i @click.stop="$router.go(-1)"></i>
        <p>
          <b></b>
          <input class="mint-header-input" type="text" :placeholder="placehStr" v-model="inputStr" autofocus="autofocus">
        </p>
        <span @click.stop="custom">搜索</span>
      </header>

      <div class="conentDiv" :style="heightStr">
          <div class="tongyi hot"> 
              <p>热门搜索</p>
              <div>
                  <span v-for="item in items" @click.stop="assignment(item)" v-if="item">{{item}}</span>
              </div>  
          </div>
          <div class="tongyi histroy" v-if="locaStrs.length != 0">
              <p class="titleP">最近搜索 <i @click.stop="clearLoac"></i></p>
              <div class="histroyConent">
                  <p @click.stop="assignment(locaStr)" v-for="locaStr in locaStrs">{{locaStr}}</p>
              </div>
          </div>
      </div>
  </div>  
</template>  
  
<script>
import {Toast,MessageBox} from 'mint-ui'
import axios from 'axios'
import util from '@/libs/util'
export default {  
  name: 'recommend',  
  data() {  
    return {  
      items:[],
      locaStrs:[],
      inputStr:null,//搜索框中文字
      placehStr:null,
      heightStr:'',
    };  
  },
  components:{
    
  },
  mounted () {
    util.toTop();
    this.ajaxHotSearch();
  },
  created (){
    this.heightStr = 'height:'+(window.innerHeight-40)+'px';
    if((window.localStorage['customStr'])){
      let customArr = (window.localStorage['customStr']).split(',');//**,**,**
      this.locaStrs = customArr;
    }
  },
  methods:{
    clearLoac(){
      window.localStorage['customStr'] = '';
      this.locaStrs = [];
    },
    custom(){
      if(!this.inputStr){
        this.inputStr = this.placehStr;
      }
      let customArr = [],customStr='';
      if((window.localStorage['customStr'])){
        customStr = window.localStorage['customStr'];
        customArr = (window.localStorage['customStr']).split(',');//**,**,**
      }
      if(customStr.indexOf(this.inputStr)<0){
        customArr.push(this.inputStr);
      }
      let newStr = customArr.join(',');
      window.localStorage.customStr = newStr;

      this.$router.replace({name:'goodsList',query:{str:encodeURIComponent(this.inputStr)}});
    },
    assignment(str){
      this.inputStr = str;
      this.custom();
    },
    ajaxHotSearch(){
      let date = {};
      let urlStr = util.getpost(date);
      axios({
        method:"POST",
        url:"/goods/open/hotSearch"+urlStr,
        dataType: 'json',
        data:date
      }).then(response => {
          this.items = response.data.body;
          this.placehStr = response.data.body[0];
      }).catch(oData => {
        if(oData.data.status && oData.data.status != 0){
          MessageBox('系统提示',oData.data.message);
        }
      })
    },
  },
  
  
};  
</script>  
  
<style lang="less" scoped> 
    @import '../assets/less/custominput.less';
    header{
      height: 0.3rem;
      background-color: #fff;
      color: #000;
      border-bottom: 1px solid #ececec;
      padding: 0.05rem 0;
      padding-left: .08rem;
      i{
        display: inline-block;
        width: 0.08rem;
        height: 0.08rem;
        line-height: 0.3rem;
        border-width: 2px 2px 0 0;
        border-style: solid;
        border-color: #888;
        transform: matrix(-.71,.71,.71,.71,0,0); 
      }
      p{
        display: inline-block;
        width: 70%;
        height: 100%;
        margin-left: 10px;
        background: #f5f5f5;
        padding-left:.3rem;
        position: relative;
        b{
          display: inline-block;
          width:.15rem;
          height:.15rem;
          position: absolute;
          top: 23%;
          left: 3%;
          background:url(../assets/images/search_icon.png) no-repeat;
          background-size:100%;
        }
        input{
          display: inline-block;
          width: 100%;
          height: 100%;
          background: #f5f5f5;
          opacity: 0.5;
        }
      }
      span{
        display: inline-block;
        width: 10%;
        height: 100%;
        text-align: center;
        line-height: 0.3rem;
        margin-left: .1rem;
      }    
    }
</style> 















